.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.blank {
  height:190px;
  background-color: black;
  color: white;
}
.contain{
  position: relative;
  left: 180px;
  bottom: 38px;
}
.index{
  position: relative;
  left: 180px;
  bottom: 38px;
  
}
.index1{
  position: relative;
  left: 70px;
  bottom: 38px;
}
.index2{
  position:relative;
  left: 140px;
  bottom: 75px;
}

  

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

 
}
